<script>
    import {doQuery} from '../utils.js';
    import {onMount} from 'svelte';

    let debit;
    let credit;
    let createDate;

    let articleID;
    let articleList = Array();

    function handleSubmit() {
        const json = JSON.stringify({
            debit: debit,
            credit: credit,
            createDate: createDate,
            article: articleList.find(article => article.id === articleID)
        });

        doQuery('/api/operation', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: json
        });
    }

    onMount(() => {
        doQuery('/api/article')
                .then(response => response.json())
                .then(json => {
                    articleList = Array.from(json);
                    articleID = articleList.length === 0 ? articleList : articleList[0].id;
                });
    });

</script>


<div class="container-fluid">
    <form on:submit={handleSubmit}>
        <div class="form-group">
            <label for="inputDebit">Debit</label>
            <input type="text" class="form-control" id="inputDebit" aria-describedby="emailHelp"
                   required min="0" bind:value={debit}/>
        </div>

        <div class="form-group">
            <label for="inputDebit">Credit</label>
            <input type="text" class="form-control" id="inputDebit" aria-describedby="emailHelp"
                   required min="0" bind:value={credit}/>
        </div>

        <div class="form-group">
            <label for="inputArticle">Article</label>
            <select id="inputArticle" class="form-control" required bind:value={articleID}>
                {#each articleList as article}
                    <option value={article.id}>{article.name}</option>
                {/each}
            </select>
        </div>

        <div class="form-group">
            <label for="inputCreateDate">Create Date</label>
            <input class="form-control" type="datetime-local" id="inputCreateDate" required bind:value={createDate}/>
        </div>

        <button type="submit" class="btn btn-primary">Добавить</button>
    </form>
</div>