<script>
    import {Router, Link, Route} from "svelte-routing";
    import {USER} from './store.js';
    import {doQuery} from './utils.js';
    import {onMount} from 'svelte';
    import Navbar from './components/Navbar.svelte';

    import Main from './route/Main.svelte'
    import EntityPage from "./components/EntityPage.svelte";
    import BalanceTable from "./components/BalanceTable.svelte";
    import ActionTemplate from "./components/ActionTemplate.svelte";
    import BalanceCreate from "./components/BalanceCreate.svelte";
    import BalanceEdit from "./components/BalanceEdit.svelte";
    import ArticleTable from "./components/ArticleTable.svelte";
    import ArticleCreate from "./components/ArticleCreate.svelte";
    import ArticleEdit from "./components/ArticleEdit.svelte";
    import OperationTable from "./components/OperationTable.svelte";
    import OperationCreate from "./components/OperationCreate.svelte";
    import OperationEdit from "./components/OperationEdit.svelte";
    import Registration from "./route/Registration.svelte";
    import AdminPane from "./route/AdminPane.svelte";

    let mounted = false;

    onMount(() => {
        doQuery('/user')
                .then(response => response.json())
                .then(json => {
                    USER.set(json);
                }).catch(() => {
            USER.set(null);
        }).finally(() => {
            mounted = true;
        });
    })
</script>

<svelte:head>
    <link rel="stylesheet" href="https://stackpath.bootstrapcdn.com/bootstrap/4.3.1/css/bootstrap.min.css">
</svelte:head>

{#if mounted}
    <header>
        <Navbar/>
    </header>

    <Router>
        <main>
            {#if $USER !== null}
                <Route path="/balance">
                    <EntityPage name="Balance">
                        <div slot="table">
                            <BalanceTable/>
                        </div>
                    </EntityPage>
                </Route>

                <Route path="/balance/create">
                    <ActionTemplate name="Balance">
                        <div slot="fields">
                            <BalanceCreate/>
                        </div>
                    </ActionTemplate>
                </Route>

                <Route path="/balance/:id" let:params>
                    <ActionTemplate name="Balance">
                        <div slot="fields">
                            <BalanceEdit id="{params.id}"/>
                        </div>
                    </ActionTemplate>
                </Route>

                <Route path="/article">
                    <EntityPage name="Article">
                        <div slot="table">
                            <ArticleTable/>
                        </div>
                    </EntityPage>
                </Route>

                <Route path="/article/create">
                    <ActionTemplate name="Article">
                        <div slot="fields">
                            <ArticleCreate/>
                        </div>
                    </ActionTemplate>
                </Route>

                <Route path="/article/:id" let:params>
                    <ActionTemplate name="Article">
                        <div slot="fields">
                            <ArticleEdit id="{params.id}"/>
                        </div>
                    </ActionTemplate>
                </Route>

                <Route path="/operation">
                    <EntityPage name="Operation">
                        <div slot="table">
                            <OperationTable/>
                        </div>
                    </EntityPage>
                </Route>

                <Route path="/operation/create">
                    <ActionTemplate name="Operation">
                        <div slot="fields">
                            <OperationCreate/>
                        </div>
                    </ActionTemplate>
                </Route>

                <Route path="/operation/:id" let:params>
                    <ActionTemplate name="Operation">
                        <div slot="fields">
                            <OperationEdit id="{params.id}"/>
                        </div>
                    </ActionTemplate>
                </Route>

                {#if $USER.roles.find(role => role === "ADMIN") !== undefined}
                    <Route path="/admin" component="{AdminPane}"/>
                {/if}
            {:else}
                <Route path="/signUp" component={Registration}/>
            {/if}
            <Route path="/" component={Main}/>
        </main>
    </Router>
{/if}

<style>
    main {
        margin-top: 1em;
    }

</style>