<script>
    import {doQuery} from '../utils.js';

    let debit;
    let credit;
    let amount;
    let createDate;

    function handleSubmit() {
        const json = JSON.stringify({
            debit: debit,
            credit: credit,
            amount: amount,
            createDate: createDate
        });

        doQuery('/api/balance', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: json
        });
    }
</script>


<div class="container-fluid">
    <form on:submit={handleSubmit}>
        <div class="form-group">
            <label for="inputDebit">Debit</label>
            <input type="number" class="form-control" id="inputDebit" aria-describedby="emailHelp" min="0"
                   required bind:value={debit}/>
        </div>

        <div class="form-group">
            <label for="inputCredit">Credit</label>
            <input type="number" class="form-control" id="inputCredit" min="0" required bind:value={credit}/>
        </div>

        <div class="form-group">
            <label for="inputAmount">Amount</label>
            <input type="number" class="form-control" id="inputAmount" aria-describedby="emailHelp" min="0"
                   required bind:value={amount}/>
        </div>

        <div class="form-group">
            <label for="inputCreateDate">Create Date</label>
            <input class="form-control" type="datetime-local" id="inputCreateDate" required bind:value={createDate}/>
        </div>

        <button type="submit" class="btn btn-primary">Добавить</button>
    </form>
</div>