<script>
    import {doQuery} from '../utils.js';
    import {onMount} from 'svelte';

    export let id;

    let mounted = false;
    let balance;

    let debit;
    let credit;
    let amount;
    let createDate;

    onMount(() => {
        doQuery(`/api/balance/${id}`)
                .then(response => response.json())
                .then(json => {
                    balance = json;
                    mounted = true;
                });
    });

    function handleSubmit() {
        const json = JSON.stringify({
            debit: debit,
            credit: credit,
            amount: amount,
            createDate: createDate
        });

        doQuery(`/api/balance/${id}`, {
            method: 'PUT',
            headers: {
                'Content-Type': 'application/json'
            },
            body: json
        });
    }
</script>

{#if mounted}
    <div class="container-fluid">
        <form on:submit={handleSubmit}>
            <div class="form-group">
                <label for="inputDebit">Debit</label>
                <input type="number" class="form-control"
                       id="inputDebit"
                       aria-describedby="emailHelp"
                       placeholder="{balance.debit}"
                       min="0"
                       required bind:value={debit}/>
            </div>

            <div class="form-group">
                <label for="inputCredit">Credit</label>
                <input type="number" class="form-control"
                       id="inputCredit" min="0"
                       placeholder="{balance.credit}"
                       required bind:value={credit}/>
            </div>

            <div class="form-group">
                <label for="inputAmount">Amount</label>
                <input type="number" class="form-control"
                       placeholder="{balance.amount}"
                       id="inputAmount" aria-describedby="emailHelp" min="0"
                       required bind:value={amount}/>
            </div>

            <div class="form-group">
                <label for="inputCreateDate">Create Date</label>
                <input class="form-control" type="datetime-local"
                       id="inputCreateDate" required
                       bind:value={createDate}/>
            </div>

            <button type="submit" class="btn btn-primary">Изменить</button>
        </form>
    </div>
{/if}
