<script>
    import {onMount} from 'svelte';
    import {doQuery} from '../utils.js'
    import {link} from 'svelte-routing';

    let OperationList = Array();

    onMount(() => {
        doQuery('/api/operation')
                .then(response => response.json())
                .then(json => {
                    OperationList = Array.from(json);
                })
    });

    function handleDelete(id) {
        doQuery(`/api/operation/${id}`, {
            method: "DELETE"
        }).then(() => {
            OperationList = OperationList.filter(operation => operation.id !== id);
        });
    }

</script>

<table class="table">
    <thead>
    <tr>
        <th scope="col">Article</th>
        <th scope="col">Debit</th>
        <th scope="col">Credit</th>
        <th scope="col">Create Date</th>
    </tr>
    </thead>
    <tbody>
    {#each OperationList as operation}
        <tr>
            <th>{operation.article.name}</th>
            <th>{operation.debit}</th>
            <th>{operation.credit}</th>
            <th>{operation.createDate}</th>
            <th><a href={`/operation/${operation.id}`} use:link>Edit</a></th>
            <th><a href="#" on:click={() => handleDelete(operation.id)}>Delete</a></th>
        </tr>
    {/each}
    </tbody>
</table>