<script>
    import {onMount} from 'svelte';
    import {doQuery} from '../utils.js'

    let users = Array();

    onMount(() => {
        doQuery("/user/all")
                .then(response => response.json())
                .then(json => {
                    users = Array.from(json);
                })
    });
</script>

<div class="container-fluid">
    <table class="table">
        <thead>
        <tr>
            <th scope="col">ID</th>
            <th scope="col">username</th>
            <th scope="col">Roles</th>
        </tr>
        </thead>
        <tbody>
        {#each users as user}
            <tr>
                <th>{user.username}</th>
                <th>{user.id}</th>
                <th>{user.roles}</th>
            </tr>
        {/each}
        </tbody>
    </table>
</div>