<script>
    import {onMount} from 'svelte';
    import {doQuery} from '../utils.js'
    import {link} from 'svelte-routing';


    let ArticleList = Array();

    onMount(() => {
        doQuery('/api/article')
                .then(response => response.json())
                .then(json => {
                    ArticleList = Array.from(json);
                })
    });

    function handleDelete(id) {
        doQuery(`/api/article/${id}`, {
            method: "DELETE"
        }).then(() => {
            ArticleList = ArticleList.filter(article => article.id !== id);
        });
    }

</script>

<table class="table">
    <thead>
    <tr>
        <th scope="col">Name</th>
    </tr>
    </thead>
    <tbody>
    {#each ArticleList as article}
        <tr>
            <th>{article.name}</th>
            <th><a href={`/article/${article.id}`} use:link>Edit</a></th>
            <th><a href="#" on:click={() => handleDelete(article.id)}>Delete</a></th>
        </tr>
    {/each}
    </tbody>
</table>