<script>
    import {onMount} from 'svelte';
    import {doQuery} from '../utils.js'
    import {link} from 'svelte-routing';


    let BalanceList = Array();

    onMount(() => {
        doQuery('/api/balance')
                .then(response => response.json())
                .then(json => {
                    BalanceList = Array.from(json);
                })
    });

    function handleDelete(id) {
        doQuery(`/api/balance/${id}`, {
            method: "DELETE"
        }).then(() => {
            BalanceList = BalanceList.filter(balance => balance.id !== id);
        })
    }

</script>

<table class="table">
    <thead>
    <tr>
        <th scope="col">Create Date</th>
        <th scope="col">Debit</th>
        <th scope="col">Credit</th>
        <th scope="col">Amount</th>
    </tr>
    </thead>
    <tbody>
    {#each BalanceList as balance}
        <tr>
            <th>{balance.createDate}</th>
            <th>{balance.debit}</th>
            <th>{balance.credit}</th>
            <th>{balance.amount}</th>
            <th><a href={`/balance/${balance.id}`} use:link>Edit</a></th>
            <th><a href="#" on:click={() => handleDelete(balance.id)}>Delete</a></th>
        </tr>
    {/each}
    </tbody>
</table>